import React, { Component } from "react";
class PrivacyPolicyPage extends Component {

  render() {

    return (
        <div>
          <div class="success-banner">
          <h1 className="privacyHeader" align="center">Privacy policy</h1>
              <div class="success-banner__wrap wrap">
              <div class="success-banner__container">
                  <span class="succeess-banner__number">

                  </span>

              </div>
              <p class="succeess-banner__paragraf">
              <hr/>
              </p>
              </div>
        </div >
        <div class="scrollBox">
          <h1 className="privacy__title privacy__title--main">Obaveštenje o obradi podataka o ličnosti (“Obaveštenje”)</h1>
          <h3 className="privacy__title">Svrha Obaveštenja</h3>
          <p>Ovim Obaveštenjem se informišu korisnici sajta dostupnog na adresi www.premesti.se (“Servis”)  o obradi  ličnih podataka koje Inicijativa „Digitalna Srbija“ (“DSI”) prikuplja kao rukovalac podataka.
            Veoma nam je važno da pažljivo pročitate ovo Obaveštenje, kako biste bili upoznati sa vrstama podatka o ličnosti koje najčešće obrađujemo, u koje svrhe, ko ima pristup, a pogotovo o vašim pravima u pogledu obrade ličnih podataka.
          </p>
          <h3 className="privacy__title">Pravni osnov obrade</h3>
          <p>Popunjavanjem upita na stranici „Gde želis da se premestiš?“ i kontakt formi „Pišite nam“, dajete nam svoje lične podatke i saglasnost da ih obrađujemo, odnosno koristimo, čuvamo i analiziramo u skladu sa važećim Zakonom o zaštiti podataka o ličnosti (“ZZPL”) i ovim Obaveštenjem. Važno je da znate da pristanak u bilo kom momentu i bez obrazloženja možete povući, što za posledicu ima prestanak dalje obrade. U slučaju opoziva, dalja obrada vaših ličnih podataka će prestati, osim u slučaju da za obradu postoji neki drugi zakonski osnov.
            Takođe, u nekim situacijama imamo legitiman interes za obradu vaših podataka, kao npr. kada je obrada neophodna u svrhu sprečavanja prevara ili eventualnih zloupotreba.
          </p>
          <h3 className="privacy__title">Svrha obrade, period i podaci koje obrađujemo</h3>
          <p>Osnovna svrha prikupljanja i obrade ličnih podataka je lakša komunikacija roditelja radi pronalaženja zamene mesta u vrtićima.  Podatke o ličnosti čuvamo za vreme koliko je potrebno radi ostvarenja konkretne svrhe, ili pak do momenta opoziva datog pristanka, odnosno do isteka vremenskog perioda od šest meseci, nakon čega se podaci brišu ili se čine neprepoznatljivim.
            Izuzetno, podatke možemo čuvati i nakon povlačenja pristanka ili ispunjenja svrhe, u situacijama kada je to neophodno radi izvršenja naše zakonske obaveze ili radi podnošenja, ostvarivanja ili odbrane pravnog zahteva.
            “DSI” prikuplja podatke o vašem imenu i prezimenu, broju telefona, adresi elektronske pošte, trenutnoj i željenoj lokaciji vrtića i uzrasnoj grupi vašeg deteta, samo kada nam se dobrovoljno dostave na ponuđenim upitima ili drugim načinom komunikacije i isključivo u svrhe odgovora na vaše zahteve.
          </p>
          <h3 className="privacy__title">Prava u vezi sa ličnim podacima koje obrađujemo</h3>
          <h5 className="privacy__subtitle">Kada DSI obrađuje podatke o ličnosti, lice na koje se podaci odnose ima:</h5>
          <ul className="privacy__list">
              <li className="privacy__item">pravo na dobijanje informacija u vezi sa obradom podataka o ličnosti, sa načinom prikupljanja  podataka i to u trenutku prikupljanja podataka;</li>
              <li className="privacy__item">pravo na pristup i druga prava u vezi sa pristupom podacima o ličnosti; </li>
              <li className="privacy__item">pravo da bude obavešten o ispravci, ograničenju, brisanju i prenosu podataka;</li>
              <li className="privacy__item">pravo na prigovor na obradu podataka o ličnosti</li>
              <li className="privacy__item">pravo na povlačenje saglasnosti, ako se obrada zasniva na pristanku ili ako se obrada vrši automatizovano;</li>
              <li className="privacy__item">pravo na obraćanje nadležnom organu: Povereniku za pristup informacijama od javnog značaja i zaštitu podataka o ličnosti – Bulevar kralja Aleksandra broj 15, 11120 Beograd, telefon: +38111 3408 900, e-mail: office@poverenik.rs;</li>
              <li className="privacy__item">pravo na prigovor na automatizovano donošenje pojedinačnih odluka, ako takva odluka po njega  proizvodi pravne posledice ili ta odluka utiče na njegov položaj.</li>
          </ul>
          <h3 className="privacy__title">Bezbednost podataka</h3>
          <p>
            DSI preduzima sve neophodne tehničke, kadrovske i organizacione mere zaštite podataka, u skladu sa Zakonom o zaštiti podataka o ličnosti i drugim relevantnim propisima.
            Vaše lične podatke nećemo činiti dostupnim trećim licima osim u svrhe odgovora na vaše zahteve bez vašeg izričitog pristanka, niti razmenjivati sa licima izvan sistema pripreme, uređivanja i tehničkog održavanja ovog sajta (koji mogu biti zaposleni u (“DSI”) ili treća lica sa kojima (“DSI”) ima ugovor o pružanju usluga izrade i održavanja ovog sajta, kao i njihovi podizvođačima). 
          </p>
          <h3 className="privacy__title">Kontakt podaci</h3>
          <p>
            Ako imate pitanja, pritužbe, zahteve ili nedoumice u vezi sa Obaveštenjem, molimo da nas kontaktirate preko adrese Inicijativa „Digitalna Srbija“, Bulevar Milutina Milankovića 11a, 11070 Beograd, Srbija, ili putem elektronske pošte na inicijativa@dsi.rs. 
            Na isti način (putem navedene adrese ili adrese elektronske pošte) možete ostvariti svoje pravo na opoziv saglasnosti kao i sva druga prava koja vam u vezi sa obradom vaših ličnih podataka pripadaju na osnovu Zakona o zaštiti podataka o ličnosti i u skladu sa njime predviđenim uslovima, odnosno pravo da zatražite informacije o obradi vaših ličnih podataka, da im pristupite i tražite njihovu kopiju, ispravku, dopunu, ažuriranje, brisanje, prekid i privremenu obustavu obrade. Dodatno, u slučaju bilo kakve nedozvoljene obrade, imate pravo na zakonom predviđenu pravnu zaštitu.
          </p>
          <h3 className="privacy__title">Izmene i amandmani</h3>
          <p>
            Svako korišćenje sajta www.premesti.se, koji je u vlasništu DSI, podleže opisanim uslovima i pravilima. Ovo je poslednja verzija Obaveštenja koju možemo promeniti u bilo koje vreme, bez prethodne najave i saglasnosti korisnika. Svaka promena Obaveštenja biće objavljena na ovoj veb-stranici i zbog toga vas pozivamo da periodično proveravate njenu sadržinu da biste blagovremeno bili informisani o eventualnim promenama.
          </p>
          <h3 className="privacy__title">Drugi sajtovi, skripte i kolačići</h3>
          <p>
            Naš sajt može sadržati linkove ka drugim sajtovima. DSI nije odgovorna za politike privatnosti ili sadržaje tih drugih sajtova.
            Tehnologije koje koristimo uključuju, ali se ne ograničavaju, na kolačiće i skripte.
            DSI takođe prikuplja podatke o hard disku ili softveru vašeg računara (IP adresa, pretraživač, domen, vreme pristupa) koji nam omogućavaju da pružimo prilagođen korisnički pristup sajtu. Prikupljeni podaci predstavljaju minimum potrebnih informacija i koriste se isključivo radi postupanja po vašim zahtevima.
          </p>
          <h3 className="privacy__title">Opšti uslovi povezivanja roditelja uz pomoć veb-sajta „Premesti.se“ </h3>
          <h4 className="privacy__title h4">1. Značenje pojedinih pojmova</h4>
          <p>
            “Korisnik”- roditelj ili staratelj maloletnog lica, koji žele da izvrše premeštanje svoje dece iz jednog vrtića u drugi putem Povezivanja;
            “Povezivanje” - omogućavanje lakšeg stupanja u kontakt Korisnika uz pomoć veb-sajta www.premesti.se;
            “Organizator”- udruženje Inicijativa „Digitalna Srbija“, Bulevar Milutina Milankovića 11a, Beograd, matični broj: 28231083, koje organizuje Povezivanje putem veb-sajta www.premesti.se;
            “Sajt”- mesto gde se realizuje Povezivanje, koje je u vlasništvu Organizatora a nalazi se na adresi www.premesti.se
            “Opšti uslovi”- svi uslovi definisani u ovom dokumentu, koji se obavezno primenjuju prilikom Povezivanja.
          </p>
          <h4 className="privacy__title h4">2. Obaveznost primene Opštih uslova</h4>
          <p>
            Svi Korisnici zainteresovani za učestvovanje u Povezivanju su u obavezi da prihvate sve uslove navedene u ovom dokumentu, a koji predstavljaju Opšte uslove.
            Ukoliko niste saglasni sa bilo kojom odredbom Opštih uslova, niste u mogućnosti da učestvujete u Povezivanju.
          </p>
          <h4 className="privacy__title h4">3. Uslovi učestvovanja u Povezivanju</h4>
          <p>
            Pravo na učešće u Povezivanju Korisnik stiče isključivo popunjavanjem upita na Sajtu koji se sastoji od:
            polja za prihvatanje ovih Opštih uslova i Obaveštenja o obradi podataka o ličnosti;
            polja za unošenje imena i prezimena, adrese elektronske pošte i broja telefona,
            polja za izbor ustanove u kojoj dete trenutno ima mesto, ustanove u koju želi da premesti dete i odabira uzrasne grupe kojoj dete pripada.
            Svi tehnički ispravno popunjeni upiti biće poslati Organizatoru na odobrenje. Upiti koji su tehnički ispravno popunjeni i u skladu su sa pravilima iznetim u ovim Opštim uslovima, biće u najkraćem roku ubačeni u sistem u kom će biti provereno da li se želja za premeštanje poklapa sa nekom od ostalih želja u sistemu. Ukoliko se ustanovi da postoji poklapanje, Korisnik će na mejl dobiti kontakt podatke drugog Korisnika za potencijalno izvršavanje zamene. Ukoliko ne postoji poklapanje, upit ostaje u sistemu dok se poklapanje zahteva ne dogodi, odnosno maksimalno šest meseci.
            Adresa elektronske pošte i broj telefona dostavljeni Organizatoru popunjavanjem Upita neće biti javno dostupni, već će biti dostupni isključivo licima unutar sistema pripreme, uređivanja i tehničkog održavanja Sajta (koji mogu biti zaposleni kod Organizatora ili treća lica sa kojima Organizator ima ugovor o pružanju usluga izrade i održavanja ovog Sajta, kao i njihovi podizvođači), kao i korisnicima s kojima se ostvaruje povezivanje.
            Obrada i zaštita svih napred navedenih ličnih i drugih podataka su u skladu sa ovim Obaveštenjem o obradi podataka o ličnosti, uključujući i zaštitu od zloupotreba, uništenja, gubitka, neovlašćenih promena i pristupa.
            Organizator ima prava da kontaktira Korisnike Povezivanja, koristeći adrese elektronske pošte dostavljene Organizatoru popunjavanjem upita, radi obezbeđivanja pristanka za uključivanje u promociju Povezivanja u sredstvima javnog informisanja.
          </p>
          <h4 className="privacy__title h4">4. Učešće u Povezivanju</h4>
          <p>
            Učešće u Povezivanju je besplatno i dobrovoljno. Korisnik ne stiče pravo na bilo kakvu naknadu od Povezivanja ili bilo kog trećeg lica po osnovu svog učestvovanja u Povezivanju, niti Organizator ima pravo da od učesnika zahteva bilo kakvu naknadu na ime uzimanja učešća u Povezivanju.
            Korisnici su odgovorni za obezbeđivanje tehničke opreme potrebne za pristupanje Sajtu. Organizator ne snosi odgovornost za bilo kakvu štetu na tehničkoj opremi nastalu korišćenjem Sajta, kao ni za pokrivanje troškova nastalih usled eventualne štete.
          </p>
          <h4 className="privacy__title h4">5. Diskvalifikacija iz Povezivanja</h4>
          <p>
            Korišćenje bilo kakve vrste kompjuterskog koda, virusa, ili bilo čega drugog što može ometati, onemogućiti, naneti štetu ili zloupotrebiti učestvovanje u Povezivanju je zabranjeno. Takvi postupci mogu dovesti do isključenja učesnika iz povezivanja i/ili zabranu učestvovanja onog ko prekrši navedena pravila. Ukoliko se posumnja da neko od učesnika na bilo koji način pokušava da prekrši pravila učestvovanja, Organizator zadržava pravo da ih isključi iz Povezivanja bez daljeg objašnjenja i obaveštavanja.
            Popunjene upite koji su na bilo koji način prema proceni Organizatora protivni pravu ili moralu, ili predstavljaju zloupotrebe sajta u komercijalne ili bilo koje druge neodgovarajuće svrhe, Organizator će ih isključiti iz daljeg procesa Povezivanja.
          </p>
          <h4 className="privacy__title h4">6. Pravo promene Opštih uslova</h4>
          <p>Organizator zadržava pravo da u bilo koje vreme izvrši izmene Opštih uslova, pisanim putem na Sajtu.</p>
          <h4 className="privacy__title h4">7. Prekid Povezivanja</h4>
          <p>
            Povezivanje se može privremeno ili trajno prekinuti odlukom Organizatora u slučaju bilo kakve zloupotrebe ili više sile, o čemu će Korisnici Povezivanja odmah biti obavešteni putem i-mejla ili pisanog obaveštenja na Sajtu.
          </p>
          <h4 className="privacy__title h4">8. Kontakt</h4>
          <p>
            U slučaju tehničkih problema, pritužbi, prijava i dodatnih pitanja povodom Povezivanja, svi Korisnici se mogu obratiti Organizatoru na i-mejl adresu inicijativa@dsi.rs.
            Takođe, Korisnici Sajta mogu prijaviti sadržaje javno dostupne na Sajtu za koje smatraju da krše neka od pravila navedenih u Opštim uslovima Povezivanja. U tom slučaju, Organizator preuzima odgovornost da u roku od sedam dana proveri da li je došlo do kršenja pravila i postupi u skladu sa time.
          </p>
        </div>
      </div>
    );
  }
}


export default PrivacyPolicyPage;
